<template>
    <el-form  :model="searchForm" ref="searchForm" :rules="searchRule" label-width="80px" size="mini" >
      <el-row>
        <el-col :span="6">
          <el-form-item label="线路" prop="lineId" width="100%">
              <el-select v-model="searchForm.lineId" multiple collapse-tags  placeholder="请选择一条或者多条" width="100%">
                  <el-option v-for="line in lines" :key="line.lineUuid" :label="line.lineName" :value="line.lineUuid"></el-option>
              </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="车辆" prop="busPlateNum">
              <el-select v-model="searchForm.busPlateNum" multiple collapse-tags  placeholder="请选择一条或者多条">
                  <el-option v-for="bus in buses" :key="bus.busUuid" :label="bus.busPlateNumber" :value="bus.busPlateNumber"></el-option>
              </el-select>
          </el-form-item>
         </el-col>
         <el-col :span="6">
          <el-form-item label="疲劳类型" prop="warnTypeId">
              <el-select v-model="searchForm.warnTypeId" multiple collapse-tags  placeholder="请选择一条或者多条">
                  <el-option v-for="warn in warntypes" :key="warn.id" :label="warn.value" :value="warn.id"></el-option>
              </el-select>
          </el-form-item>
         </el-col>
         <el-col :span="6">
          <el-form-item label="行为标记" prop="warnMarkId">
              <el-select v-model="searchForm.warnMarkId"   placeholder="请选择">
                  <el-option v-for="mark in warnMarks" :key="mark.id" :label="mark.value" :value="mark.id"></el-option>
              </el-select>
          </el-form-item>
         </el-col>
       </el-row>
       <el-row>
         <el-col :span="8">
            <el-form-item label="起始时间" prop="notifyTime">
                <el-date-picker
                  style="width:100%"
                  v-model="searchForm.notifyTime"
                  type="datetimerange"
                  :picker-options="dateBefore"
                  :default-time="[oftenTime, oftenTime]"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"/>
            </el-form-item>
         </el-col>
         <el-col :span="5">
            <el-form-item label="司机" prop="driverName">
                <el-input v-model="searchForm.driverName" placeholder="请输入司机姓名"></el-input>
            </el-form-item>
         </el-col>
        <el-col :span="6">
            <el-form-item label="标记人" >
                <el-radio-group v-model="searchForm.warnMarkUser">
                    <el-radio :label="0">仅自己</el-radio>
                    <el-radio :label="1">全部</el-radio>
                </el-radio-group>
            </el-form-item>
         </el-col>
         <el-col :span="5" class="flex-right">
         <el-form-item>
              <el-button type="primary" size="mini" @click="search">查询</el-button>
              <el-button  size="mini" @click="resetForm">重置</el-button>
          </el-form-item>
         </el-col>
      </el-row>
    </el-form>
</template>
<script>
import moment from 'moment'
// api
import { linesList, busesList, behaviorAlarmType, warnMarksList } from '@/api/common'
export default {
  name: 'StatisticalQuerySearch',
  data () {
    return {
      lines: [],
      buses: [],
      warntypes: [],
      warnMarks: [],
      searchForm: {
        notifyTime: ['', ''],
        lineId: [],
        busPlateNum: [],
        warnTypeId: [],
        driverName: '',
        warnMarkId: '',
        warnMarkUser: 1,
        startTime: '',
        endTime: ''
      },
      searchRule: {},
      dateBefore: {
        disabledDate: (time) => {
          // return moment(time).diff(moment(), 'd') > -1
          return time.getTime() > new Date().getTime()
        }
      },
      oftenTime: moment().format('HH:mm:ss')
    }
  },
  watch: {
    'searchForm.notifyTime': function (time) {
      if (time.length) {
        this.searchForm.startTime = moment(time[0]).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
        this.searchForm.endTime = moment(time[1]).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
      }
    }
  },
  mounted () {
    // 初始化后先根据默认条件查询数据
    this.initTime()
    linesList({}).then(res => {
      this.lines = res
    })
    busesList({}).then(res => {
      this.buses = res
    })
    behaviorAlarmType({}).then(res => {
      this.warntypes = res
    })
    warnMarksList({}).then(res => {
      this.warnMarks = res
    })
    this.search()
  },
  methods: {
    initTime () {
      const startTime = moment().subtract(6, 'd').utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
      const endTime = moment().subtract(0, 'd').utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
      this.searchForm.notifyTime = [startTime, endTime]
      this.searchForm.startTime = startTime
      this.searchForm.endTime = endTime
    },
    // 转换查询参数为后端需要的参数格式
    convertSearchParams () {
      const searchParams = {
        lineId: this.searchForm.lineId,
        busPlateNum: this.searchForm.busPlateNum,
        warnTypeId: this.searchForm.warnTypeId,
        driverName: this.searchForm.driverName,
        warnMarkId: this.searchForm.warnMarkId,
        warnMarkUser: this.searchForm.warnMarkUser,
        startTime: this.searchForm.startTime,
        endTime: this.searchForm.endTime
      }
      return searchParams
    },
    search () {
      this.$emit('search', this.convertSearchParams())
    },
    resetForm () {
      this.$refs.searchForm.resetFields()
      this.searchForm.warnMarkUser = 1
      this.initTime()
      this.$emit('rest', this.convertSearchParams())
    }
  }
}
</script>



